.events-preview{
  display: flex;    
  flex-direction: column; 
  padding:10px;   
  border-radius: 20px;    

}
.events-preview-header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}
.events-preview-list { 
 display: grid;
 grid-gap: 40px; 
 grid-auto-flow: column;
 grid-template-rows: 1fr; 
 border-radius: 2px;
 text-align: center;  
 padding: 20px;
 justify-items: center;
 align-items: center; 
 overflow-x: auto;
 overflow-y: hidden;
 -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */

}

/* Hide scrollbar for Chrome, Safari and Opera */
.events-preview-list::-webkit-scrollbar {
  display: none;
}

/* TABLET */
@media screen and (max-width: 830px){
  .events-preview-header{
    font-size: 1em
  }

.events-preview-list { 
  display: grid;
  grid-gap: 40px; 
  grid-auto-flow: column;
  grid-template-rows: 1fr; 
  border-radius: 2px;
  text-align: center;  
  padding: 20px;
  justify-items: center;
  align-items: center; 
  overflow-x: auto;
  overflow-y: hidden;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */

}
  
}

/* MOBILE */
@media screen and (max-width: 520px){
  .events-preview-header{
    font-size: 0.75em
  }

  .events-preview-list { 
    display: grid;
    grid-gap: 40px; 
    grid-auto-flow: column;
    grid-template-rows: 1fr; 
    border-radius: 2px;
    text-align: center;  
    padding: 20px;
    justify-items: center;
    align-items: center; 
    overflow-x: auto;    
    overflow-y: hidden;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */

}
  
}