.event-preview-card{  
    background-color: rgb(247, 244, 244);      
    display: flex;
    flex-direction: column;     
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); 
    height: 350px;             
    width: 25vw;
}

.event-preview-card:hover{
   background-color:lightgray;
}

.event-picture{               
   width: 100%; 
   height: 60%;   
   background-image: linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.2)),url("../../assets/online-business.png");
   background-size: cover;
   background-repeat:no-repeat;      
   background-position: center;
   background-repeat: no-repeat;
   border-top-left-radius: 10px;
   border-top-right-radius: 10px;
   margin-left: auto;
   margin-right: auto;   
}

.event-details{
   margin: 0px 10px;
}

.event-popularity {
   display: inline-flex;
   position: relative;   
   background-color: rgba(0, 0, 0, 0.80);
   color: white;
   padding:6px;
   margin:6px;
   border-radius: 3px;
   font-size: 12px;
   position: relative;    
   width: 50%;
   height: 15px;
   top: 45%;
   left:80px;
   border-radius: 10px;
   font-size: 1em;
   justify-content: left;
   display: none;
}

.heart-icon{   
   position: relative;
   left: 35%;
   top: 100%;
   color: red;
   display: none;
   
}

/* TABLETS */
@media screen and (max-width:830px){
  .event-preview-card{
     height: 250px;
     width: 30vw;     
  }

  .event-picture{               
   width: 100%; 
   height: 60%;   
   margin-top: 0px;  
}

  .event-name{
   font-size: 1em;
  }

  .event-host{
   font-size: 1em;
  }
   
}

/* MOBILE */
@media screen and (max-width:520px){
  .event-preview-card{
     height: 200px;
     width: 35vw;    
  }

  .event-picture{               
   width: 100%; 
   height: 60%;   
   margin-top: 0px;  
}

  .event-name{
   font-size: 0.75em;
  }

  .event-host{
   font-size: 0.75em;
  }
   
}